<template lang="html">
  <v-container  class="bg-blue">
    <v-btn color="white" @click="toggleTheme">Змінити тему на: {{theme.global.name.value != "dark"?"темну":"світлу"}}</v-btn>
  </v-container>
</template>

<script>
import { useTheme } from 'vuetify';
let settings={
  theme: "light"
}
export default {
  data(){
    return{
      hi: "hello",

    }
  },
  setup () {
    const theme = useTheme();
    return {
      theme,
      toggleTheme(){
        settings.theme = theme.global.name.value = theme.global.current.value.dark ? 'light' : 'dark';
        localStorage.setItem('settings', JSON.stringify(settings));
      }
    }
  }
}
</script>

<style lang="css" scoped>
</style>
